.button {
  background-color: #9c0018;
  border: none;
  color: white;
  padding: 16px 32px;
  text-decoration: none;
  margin: 4px 0px 0px 0px;
  cursor: pointer;
  width: 100%;
}

.button:hover {
  background-color: slategray;
}

.input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  transition: width 0.4s ease-in-out;
}

.input:focus {
  background-color: slategray;
}
.select {
  width: 100%;
  padding: 16px 20px;
  border: none;
  border-radius: 4px;
  background-color: #f1f1f1;
}

